import React, { Component } from "react";
import { createNumberMask } from "redux-form-input-masks";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { connect } from "react-redux";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

class AccordCashMurni extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  componentDidMount() {
    var cash = 0,
      harga = 0,
      netto = 0;
    var local = JSON.parse(localStorage.getItem("alatBayarTunaiRp"))
      ? JSON.parse(localStorage.getItem("alatBayarTunaiRp"))
      : "kosong";
    if (local === "kosong") {
      cash = 0;
      harga = 0;
      netto = 0;
    } else {
      cash = local[0].cash_rp;
      harga = local[0].harg_emas;
      netto = local[0].cash_netto;
    }
    this.props.change("cash", cash);
    this.props.change("harga", harga);
    this.props.change("cashNetto", netto);
  }

  onChange() {
    this.props.change("cashNetto", this.props.cashNettoMurni);
  }
  render() {
    return (
      <div className="col-lg-6 mt-3">
        <div id="accordion" className="custom-accordion mb-4">
          <div className="card mb-0 bg-dark">
            <div className="card-header " id="headingOne">
              <div className="row">
                <div className="col-lg-8">
                  <h5 className="m-0">
                    <a
                      className="custom-accordion-title d-block pt-2 pb-2 text-white"
                      data-toggle="collapse"
                      href="#collapseCash"
                      aria-expanded="true"
                      aria-controls="collapseCash"
                    >
                      Cash
                    </a>
                  </h5>
                </div>
                <div className="col-md-4 col-lg-4">
                  <label htmlFor="" style={{ color: "white" }}>
                    Netto
                  </label>
                  <Field
                    name="cashNetto"
                    type="telp"
                    component="input"
                    className="form-control"
                    readOnly={true}
                    {...currencyMask}
                  />
                </div>
              </div>
            </div>
            <div
              id="collapseCash"
              className="collapse"
              aria-labelledby="headingOne"
              data-parent="#accordion"
            >
              <div className="card-body bg-white">
                <form onSubmit={this.props.handleSubmit}>
                  <div className="row">
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">Cash</label>
                      <Field
                        name="cash"
                        component="input"
                        type="tel"
                        className="form-control"
                        onBlur={this.onChange()}
                        {...currencyMask}
                      />
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">Harga </label>
                      <div className="form-gorup">
                        <Field
                          name="harga"
                          component="input"
                          type="tel"
                          className="form-control"
                          onBlur={this.onChange()}
                          readOnly={true}
                          {...currencyMask}
                        />
                      </div>
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <Field
                        name="cashNetto"
                        label="Netto"
                        type="telp"
                        component={renderField}
                        readOnly={true}
                        {...currencyMask}
                      />
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button type="submit" className="btn btn-primary">
                          Simpan
                        </button>
                      </div>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

AccordCashMurni = reduxForm({
  form: "AccordCashMurni",
  enableReinitialize: true,
})(AccordCashMurni);
const selector = formValueSelector("AccordCashMurni"); // <-- same as form name
export default connect((state) => {
  const { harga, cash } = selector(state, "harga", "cash");
  return {
    cashNetto: (parseFloat(cash) / parseFloat(harga)).toFixed(3),
    cashNettoMurni: parseFloat(cash),
  };
})(AccordCashMurni);
